import React from "react";

export default function TopGPDataAll() {

  const gpArray = [
    {
      id: 1,
      name: "Punsari GP",
      desc: "Child-friendly and all-round development, winner of national awards",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/gp1.png'),
      gpImgMobile: require('../../assets/gp-success/mgp1.png'),
      content: <React.Fragment>
Punsari Gram Panchayat (population 6,000) of Taluka Talod, District Sabarkantha, Gujarat is located in Sabarkantha district in the state of Gujarat, India. The village is located at about 80 KM from the state capital, Gandhinagar. Known as a smart village, Punsari is equipped with effective and efficient programs ranging from early childhood development to womens empowerment and to modern infrastructure such as CCTV camera and public announcement system. It has received about 80,000 visitors so far from national and international entities, and is a model of good governance.<br/><br/>
<strong>Governance: </strong> Punsari has undergone transformation under the leadership of the Sarpanches: Mr. Himanshu Patel (2006-2016) and Sunandaben Patel (2016-to date) (photos on the right, top first and second respectively). Gram Panchayat Coordination Committee (GPCC) was formed and is active. There were 228 Below Poverty Line (BPL) families in the village in 2011, now reduced to 60. <br/><br/>
In 2006, the GP had Rs. 2 lakh in the bank, and in 2012, it reached a fixed deposit of Rs. 1.5 crore. This was achieved through well managed funds. Punsari has received several awards, including the Best Gram Panchayat of Gujarat (2010-11) and Nirmal Gram Award by the Central Government. <br/><br/>
<strong>Child Nutrition and Education: </strong>  Child malnourishment rate is zero percent,  achieved by eight well constructed and well run AWCs. All AWCs have functional toilets with soaps and napkins for the children. Total 100% enrolment of all eligible children in schools is ensured every year, and 0% drop out rate is ensured through girls and boys from poor background households adopted by well-to-do families for education support. Atal Express minibus (20 seats) provide free commuting for students to five primary schools (up to Standard 5) and four secondary schools (up to Standard 8). The schools are equipped with the classrooms with CCTV Cameras, well equipped computer labs, books library, playgrounds, mineral water plants, separate toilets for girls and boys (cleaned regularly), effective midday meal (plus one special meal per month). <br/><br/>
<strong>Women empowerment: </strong>  There are 108 womens Self-help Groups (SHGs) with a total 1,200 membership; 90 SHGs linked with banks for loans and have revolving funds. Training programs were organised in sewing, henna (Mahendi), beauty parlour, pottery etc. Women have eight shops at a government built shopping centre for selling their produce. <br/><br/>
<strong>Public Health, drinking water, sanitation, agriculture and other sectors: </strong>  Maternal and infant mortality rate is zero per cent. Primary health centre operates 24x7 with maternity ward, medicine shop and library. Piped chlorinated water is supplied to every house through five bore wells. Also there are four hand pumps and a Reverse Osmosis (RO) plant for water purification. All 73 village wells are regularly recharged with rainwater. All houses are covered with sanitation units, and the whole village is covered with an underground drainage system for waste and stormwater management. Pay-and-use public toilets are accessible near the bus station. Plantation of fruit and medicinal plants has been done in barren lands. Punsari has a well managed crematorium for last rites, and mortal remains (asthi) are kept in pots/urns in Gram Panchayat for collective dispersal at Haridwar or other religious sites once in a year. <br/><br/>

<strong>Leadership: </strong>  The Sarpanch (Himanshu Patel) had started with formation of Gram Panchayat Coordination Committee (GPCC) by involving all the Gram Panchayat members, frontline workers and officers of the government line departments, and operators of Fair Price Shop and Midday Meal. The frontline workers and others had few challenges in the beginning as they were not used to such coordination, but strong will of the Sarpanch resulted in regularising the GPCC forum. <br/><br/>

Childrens participation was ensured through activities such as village newsletter.
</React.Fragment>
,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Punsari GP.pdf"),
    },
    {
      id: 2,
      name: "Jethipura GP",
      desc: "Offers exemplary work on nutrition and education for children",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/gp2.jpg'),
      gpImgMobile: require('../../assets/gp-success/gp2.jpg'),
      content: <React.Fragment>Jethipura Gram Panchayat (population as per 2011 Census: total 1242, females 608, males 634) of Idar Block, consisting of one village is located in Sabarkantha district in the state of Gujarat, India. The village is located at about 15 KM from Idar (nearest town) and 28 KM from Himatnagar, the district headquarters. Childrens population below 6 years includes 122 (female 67, male 55). The population includes 299 persons (135 females, 164 males) from the SC community.<br/><br/>
Recipient of many awards, Jethipura is equipped with effective and efficient programs ranging from early childhood development to womens empowerment and to modern infrastructure such as CCTV camera and LCD street lights. It has received hundreds of visitors so far from multiple states of India. The village has a long history of unity among all the people from all the castes, and Gram Panchayat Sarpanch and members have always been selected in Gram Sabha - never elected. The people here believe that the election creates sub-groups within the village and affects unity. <br/><br/>
<strong>Governance:</strong> The village has a total 32 committees, including SMC, VHSNC, Pani Samiti and others. All are active. There are 4 Gram Sabhas organised in a year with presence of more than 50 members in each. The GP has CCTV cameras with 24x7 surveillance. There is no crime in the village. The village has received several awards, including a national award amounting to Rs. 18 lakh. The awards include Smart Village Award for the year 2017-18 and Mata Yashoda Award (Rs. 21,000/-) for best Anganwadi centre in the state. <br/><br/>
<strong>Child Nutrition and Education:</strong> There are two Anganwadi centres and no malnourished child in the whole village. The AWCs are also serving 13 pregnant women currently (2020). There has been a Primary School in the village since 1929 having 1st to 8th Standards. There are 137 pupils and 7 teachers. English is taught from 3rd Standard. For the 7th and 8th standards, there are smart classes with computers. There are 13 members in SMC, and its meeting is regular. <br/><br/>
The village has had a library since pre-independence time, and is linked with the block level library. Membership fee is Rs. 400 per year for reading books, newsletters etc. The library also has a computer and the Internet with online resources with Rs. 2,400 membership fee per year. Therefore, it is running sustainably. The library provides support to the youth with resources to prepare for the competitive exams and employment. <br/><br/>
<strong>Drinking Water and Sanitation: </strong> The GP has installed a water purification plant and every household has been covered with clean drinking water. There was a major challenge of solid waste management, as there was no common land. The GP identified a piece of agricultural land and leased it for solid waste deposit. The village now remains clean. <br/><br/>

<strong>Leadership:</strong> Mr. Ahesanbhai, Sarpanch and Talati, Mr. Prakashbhai are working in synergy and in participation of all the people. The GP has income from house tax at Rs. 450 per house per year, and it is recovered regularly. Incomes of the GP include Rs. 1.5 lakh tax revenue per year, grants from the Finance Commission and government schemes, Rs. 6.00 lakh for Samras (state government prize for selection of the Sarpanch and Panchayat members with consensus of the whole village, without the need of the election).
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Jethipura GP.pdf"),
    },
    {
      id: 3,
      name: "Daramli GP",
      desc: "Exemplary child-friendly and multiple development activities",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/book5.jpg'),
      gpImgMobile: require('../../assets/gp-success/mbook5.jpg'),
      content: <React.Fragment>Daramli Gram Panchayat (population as per 2011 Census: total 1,529, females 730, males 799) of Idar Block, consisting of one village is located in Sabarkantha district in the state of Gujarat, India. The village is located at about 10 KM from Idar (nearest town) and 23 KM from Himatnagar, the district headquarters. Childrens population below 6 years includes a total of 171 (female 61, male 110). The population includes 55 persons (24 females, 31 males) from the SC community.<br/><br/>

Recipient of many awards, Daramli is equipped with effective and efficient programs ranging from early childhood development to womens empowerment and to modern infrastructure such as CCTV camera and public announcement system. It has received hundreds of visitors so far from multiple states of India. <br/><br/>
<strong>Governance:</strong> Mrs. Hetalben was elected as Sarpanch for the first time in her first term about 8 years ago. Then in the second term about 4 years ago, she was selected as Sarpanch unanimously by the villagers due to her good work. Under her leadership, 65 to 70 speakers and mikes (announcement system) have been installed in the village. Common announcements are made using the system and religious music is played during the mornings and evenings. The GP has installed CCTV cameras also on the streets. <br/><br/>
<strong>Child Nutrition and Education:</strong> There are two Anganwadi Centres (AWCs) in the village. The Anganwadi No. 1 is a model AWC having 28 children of 7 months to 3 years of age and 23 children of 3-6 years of age; and the AWC no. 2 has 18 and 17 children respectively in the two separate age groups. There are 17 pregnant women and 11 lactating women served by both the AWCs. The AWCs also support the adolescent girls. There is only one child malnourished, whose weight at birth was only 1.5 KG, but now after 8 months, he is 5 KG. In schools, the GP has promoted childrens banks. The school children deposit some amounts from their pocket money in these banks, and they can withdraw that money for use once a year for educational purposes. <br/><br/>
<strong>Women Empowerment and Vocational Training:</strong> There are 22 self-help groups of women that are actively engaged in savings and credit activities. Vocational training (e.g. tailoring, beauty parlour, soap making etc.) have been imparted to 251 youth of the village, and 51 of them are now earning incomes. <br/><br/>
Every year, 50 to 100 trees are planted in the village and 45 youth have been engaged to take care of the trees. <br/><br/>
<strong>Leadership:</strong> To begin with as Sarpanch, Mrs. Hetalben had met with all the people of the village (children, women, elderly women, men and others) to understand their needs holistically. She and her husband took up many no cost and low cost activities to serve the people. Such activities include registration of youth who wish to donate blood and can be called when needed; asthi (mortal remains) bank that is collected throughout the year and dispersed once in a year in the Ganges at Haridwar or similar place of significance; and trees plantation. The GP has Rs. 770/- house tax per year that includes services of water supply, sanitation and electricity. One time tax of Rs. 5,000/- is levied for a new house construction in the GP. Professional tax from the shops, petrol pump, NGOs and other similar entities is Rs. 200 per month.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Daramli GP.pdf"),
    },
    {
      id: 4,
      name: "Kukma GP",
      desc: "Inspiring ideas and work on Girls Education",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/gp4.jpg'),
      gpImgMobile: require('../../assets/gp-success/mgp4.jpg'),
      content: <React.Fragment>Kukma Gram Panchayat (population as per 2011 Census: total 7,216, females 3,483, males 3,733) of Bhuj Block is located in Kutch district in the state of Gujarat, India. The village is located at about 10 KM from Bhuj, the nearest town and the district headquarters. Childrens population below 6 years includes a total of 986 (female 458, male 528). The population also includes 1,547 persons (739 females, 808 males) from the SC community and 64 persons (32 females, 32 males) from the ST community.  Recipient of Deen Dayal Upadhyay Sashaktikaran Award 2020, Kukma is equipped with effective and efficient programs ranging from early childhood development to womens empowerment and to modern infrastructure such as CCTV cameras. <br/><br/>
<strong>Governance:</strong> Kukma has undergone transformation under the leadership of Mrs. Kankuben Vankar, Sarpanch. There are 27 CCTV cameras, which help to monitor cleanliness and other amenities and services. One public park was renovated for children to play. One more garden work is ongoing. All the committee meetings in the village are regular with discussions and actions taken on the required matters. <br/><br/>
<strong>Child Nutrition and Education:</strong> There are no malnourished children in the village (thought it was one of the challenges earlier), and the GP members visit the AWCs regularly to support the AWCs. The GP ensured 100% enrolment and attendance of girls in schools by organising local transport, which was otherwise identified as a challenge. Mrs. Kankuben created an innovation: the girl with the highest education in a ward was recognised by naming a street with her name e.g. Sonali Marg (street) as in the picture beside. There are 30 streets with such girls names in Kukama. <br/><br/>
<strong>Women empowerment:</strong> On 10 March 2019, womens day was celebrated by encouraging girls to choose their model women and present it. Eight girls had participated, and model women were given awards. <br/><br/>
<strong>Public Health:</strong> Immunisation was done by the health department, but the Sarpanch reviewed the same, and provided local support, resulting in 100% children covered. <br/><br/>
<strong>Child Protection:</strong> The GP addressed the harassment issue of the girls by boys. Safety audit was conducted with the support of Kutch Mahila Vikas Sangathan (a local NGO) and a resolution was passed to take strict actions with a copy given to the police department. <br/><br/>
<strong>Drinking Water and Sanitation:</strong> clean drinking water has been ensured by CSR funds. Safai Abhiyan (sanitation campaign) was conducted through street plays, and the CCTV cameras are helping to monitor if anyone is littering. As a result, the village is clean. <br/><br/>
<strong>Other:</strong> Liquor use was also a challenge in the village. The GP passed a resolution to impose a fine of Rs. 2,500 for the person drinking or making liquor, and Rs. 1,500 reward for the person providing information. <br/><br/>
<strong>Leadership:</strong> There are 10 Anganwadi Centres (AWCs) in the village. In 2018, there were 6 Severely Acute Malnourished (SAM) children in the village. The well-to-do families were encouraged, who adopted (i.e. informally took responsibility for) these children, and provided support until the children became normal. The Sarpanch and GP ward members are visiting the AWCs regularly and conducting monitoring. Kukma is a large village. As the girls from far away wards (e.g. Ashapura and Avadh colonies) had to walk 2.5 KM to reach school, they used to drop-out. GP arranged local transport with 65% cost paid  by GP and 35% by parents. This ensured 100% enrolment and attendance of girls in schools. The girls continuing education were encouraged by naming the streets on their names.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Kukma GP.pdf"),
    },
    {
      id: 5,
      name: "Kunariya GP",
      desc: "Visionary good governance for children and all",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/gp5.jpg'),
      gpImgMobile: require('../../assets/gp-success/mgp5.jpg'),
      content: <React.Fragment>Kunariya Gram Panchayat (population as per 2011 Census: total 2,533, females 1,246, males 1,287) of Bhuj Block is located in Kutch district in the state of Gujarat, India. The village is located at about 21 KM from Bhuj, the nearest town and the district headquarters. Childrens population below 6 years includes a total of 594 (female 304, male 290). The population also includes 216 persons (110 females, 106 males) from the SC community and 49 persons (26 females, 23 males) from the ST community.  Kunaria planned its own development by analysing its local needs and planning for the same through participatory manner, as seen in the picture. Adolescents Justice Committee was formed, who participate regularly in Gram Sabha and contribute their inputs. <br/><br/>
<strong>Governance:</strong> Sureshbhai is the Sarpanch of Kunariya village since January 2017. He took initiative to visit 14 good Gram Panchayats of different states for his own learning. In 2017, Sureshbhai conducted a total of 87 events of awareness in different sectors (education, health, water, sanitation etc.), which created active participation of people. <br/><br/>
Total 7 to 8 Gram Sabhas are organised in a year with nearly 50% women participating. The Gram Panchayat Coordination Committee (GPCC) was formed with 23 members in 2018, and it is working actively. ANMs were given best practice awards, and such recognition contributes to good mutual cooperation among all members. <br/><br/>
<strong>Education:</strong> Before he became Sarpanch, there were 41,000 midday meal attendance by children. By 2018-19, he raised it to 47,000 by conducting monthly reviews with schools. The <a href="https://www.youtube.com/watch?v=kiGHIyooqac" target="_blank" rel="noreferrer">Link</a>. Considering Covid-19 situation, the primary school started a YouTube channel for the teaching-learning process. <br/><br/>
<strong>Public Health and Drinking Water:</strong> He focused on children and womens health to start with. From pregnancy to healthy delivery, proper tracking is ensured by supporting the Auxiliary Nurse Midwife (ANM). He made the VHSNC active, resulting in achievement of 100% immunisation of children. To address long term challenges of drinking and irrigation water, village groundwater mapping was conducted with technical experts support, and 6 maps were prepared. Coordination with 26 frontline workers from 39 line departments was ensured in 2018. <br/><br/>
<strong>Agriculture/livelihood/forestry:</strong> Considering Covid-19 lockdown and related employment issues, MGNREGA work was started by benefiting 43 households, resulting in assured incomes, especially for women. Kunariya has 35 artisan families who make copper bells and 87 women doing embroidery work. Marketing support was provided to them for selling items during the lockdown time. Under MGNREGA, Kunariya GP has been planting 5,000 tree saplings every year since 2017. So far 63,000 trees have been planted and about 57,000 have grown up. <br/><br/>
Kunariya GP also runs a blog at kunariyapanchayat.blogspot.com. It is active in terms of posting information of the events taking place in Kunariya. <br/><br/>
<strong>Leadership:</strong> The Gram Panchayat Coordination Committee (GPCC) was formed with 23 members in 2018, and it is working actively. ANMs were given best practice awards, and such recognition contributes to good mutual cooperation among all members. Similarly, the School Management Committee (SMC) was also made active by monitoring attendance of children and teachers in schools. SMC meetings take place regularly. A local Covid assessment was conducted by forming a team of ASHA Workers, Anganwadi Workers, ANM and youth, resulting in identifying 87 vulnerable households, followed by support to them. Arrangement with IOC bank was made so that a bank representative came to the village once in a week to provide cash, so that the people do not have to go to Bhuj city.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Kunariya GP.pdf"),
    },
    {
      id: 6,
      name: "Dhrobana GP",
      desc: "A society-wide initiative on child marriage prevention",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/book5.jpg'),
      gpImgMobile: require('../../assets/gp-success/mbook5.jpg'),
      content: <React.Fragment>Dhrobana Gram Panchayat (population as per 2011 Census: total 8,098, females 3,963, males 4,135) of Bhuj Block is located in Kutch district in the state of Gujarat, India. The village is located at about 86 KM from Bhuj, the nearest town and the district headquarters. Childrens population below 6 years includes a total of 1,939 (female 947, male 992). The population also includes 252 persons (118 females, 134 males) from the SC community and 4 persons (1 females, 3 males) from the ST community. The village population is comprised mainly of Muslim community.<br/><br/>
<strong>Child Marriage:</strong> Mr. Siddikbhai is the Sarpanch and he is also a leader of his community. The major challenge addressed by the Sarpanch includes child marriage. A marriage was taking place in the village during early 2020. The bride was ready with a bridal suit. At such a critical time, a neighbour informed the Sarpanch that the bride was below 18 years of age. In urgent consultation with the District Child Protection Officer, the marriage was stopped. Later on, all the villagers were informed to stop child marriage practice. Mr. Siddikbhai also took up the same issue in their broader community (involving multiple villages) and guided the people to stop the child marriage practice.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Dhrobana GP.pdf"),
    },
    {
      id: 7,
      name: "Soyla GP",
      desc: "School Management Committees persistent efforts ensure children passing 12th Standard for the first time in the village history",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/book5.jpg'),
      gpImgMobile: require('../../assets/gp-success/mbook5.jpg'),
      content: <React.Fragment>Soyla Gram Panchayat (population as per 2011 Census: total 452, females 220, males 232) of Bhuj Block is located in Kutch district in the state of Gujarat, India. The village is located at about 67 KM from Bhuj, the nearest town and the district headquarters. Childrens population below 6 years includes a total of 70 (female 36, male 34). The village is largely populated by Muslim community. <br/><br/>
Being a remote and small village in a dry vast land area, Soyla is an economically poor village. The primary school has 1-8 standards. There are a total of 73 students, including 33 girls. Addition of the 8th standard was the achievement of the SMC (School Management Committee), as a result of its members applying for and persuading the district authorities.  For the 9th standard, the students have to go to Khari village, 3 KM away, and for 11 and 12 standards, to Khavda, the Taluka place, 11 KM away. <br/><br/>
Due to the above challenge of distance, the children used to drop out after 8th or 9th standard, and none ever passed 12th standard. The SMC arranged for local transport for the school girls and boys. Due to the SMCs efforts, the children passed 12th standard last year (2019) for the first time. <br/><br/>

<strong>Leadership:</strong> Mr. Ramzanbhai, Elected Representative of the Gram Panchayat and member of the SMC, regularly visits the school, checks attendance of the children, and attends all the SMC meetings. SMC decisions are coordinated with the GP for support. In the SMC meetings, important decisions related to drinking water (for example, RO plant and piped water) were made and effectively implemented. Setu Abhiyan has been providing support in the education sector in this village since 2008 by awareness generation activities through street plays and community meetings. Setu Abhiyan is also supporting the GPDP process through facilitating Mahila Sabha and Gram Sabha. Soyla GP has 8 wards with 150 households, and Ward Sabhas are also organised for GPDP.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Soyla GP.pdf"),
    },
    {
      id: 8,
      name: "Amardi GP",
      desc: "Sarpanchs efforts results in improved Anganwadi and School services for girls and boys",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/book5.jpg'),
      gpImgMobile: require('../../assets/gp-success/mbook5.jpg'),
      content: <React.Fragment>Amardi Gram Panchayat (population as per 2011 Census: total 1,213, females 639, males 574) of Bhachau Block is located in Kutch district in the state of Gujarat, India. The village is located at about 62 KM from Bhuj (district headquarters), and 20 KM from Bhachau, the nearest town and the block headquarters. Childrens population below 6 years includes a total of 142 (female 67, male 75). The population also includes 183 persons (93 females, 90 males) from the SC community and 6 persons (3 females, 3 males) from the ST community.<br/><br/>
<strong>Governance:</strong> Amardi has undergone transformation under the leadership of Mrs. . is the Sarpanch. She and her husband (retired employee of the forest department) are very active in village development. They have installed 16 CCTV cameras in 2019 with a cost of Rs. 2.5 lakh. It helps to monitor the village streets in terms of cleanliness. <br/><br/>
<strong>Child Nutrition and Education:</strong> In Anganwadi centre, they did not have an Anganwadi Worker, and the centre was run only by Anganwadi Helper providing food, but with no pre-school education activities. As an immediate step, they arranged a temporary Anganwadi Worker at a cost of Rs. 1,500 per month. Then the Panchayat members actively liaised with the block and district level, and ensured appointment of a regular Anganwadi Worker. In the education sector, they started online tuitions during school closure due to Covid-19 challenge. In school, Bal Sabha was organised and childrens committees were formed for supervision of midday meal, WASH facilities and childrens regular attendance. The childrens committees presented the issues in the GP. The issues included a loosely hanging electric pole near the school (posing risk), repairs required in classrooms and vacancy of a teacher. These issues were addressed by the GP. <br/><br/>
<strong>Women empowerment:</strong> The GP identified 9 widow women and supported them in applications for government scheme benefits. <br/><br/>
<strong>Sanitation:</strong> The GP has constructed separate toilets for girls and boys in school at a cost of Rs. 1.90 lakh. Clean piped water has also been provided. The GP installed a waste water drainage system in the village. <br/><br/>
The GP carried out tree plantation and arranged for regular watering. <br/><br/>

<strong>Leadership:</strong> Mrs. _________ and her husband, Mr. ___________, actively worked to improve the situation of the village. UNNATI, an Ahmedabad based NGO with field office in Bhachau, has been providing support to the Amardi Gram Panchayat since ________.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Amardi GP.pdf"),
    },
    {
      id: 9,
      name: "Khokhra Umar GP",
      desc: "A tribal village Sarpanch enables students to go beyond 10th Standard education",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/book5.jpg'),
      gpImgMobile: require('../../assets/gp-success/mbook5.jpg'),
      content: <React.Fragment>Khokhra Umar Gram Panchayat (population as per 2011 Census: total 1,810, females 901, males 909) of Dediapada Block is located in Narmada district in the state of Gujarat, India. The village is located at about 48 KM from Rajpipla (district headquarters), and 3 KM from Dediapada, the nearest town and the block headquarters. Childrens population below 6 years includes a total of 224 (female 102, male 122). The village is populated mainly by tribal people, and others include only 34 persons (9 females, 25 males).<br/><br/>
<strong>Governance:</strong> Mr. Dineshbhai has been Sarpanch since 2015. His motivation is to do such work in the GP that the people always remember him. GPDP is prepared every year. Gram Sabha has a presence of 60 to 70 people, and at least 2 Gram Sabhas are held in a year. There are many committees, including PESA committees, but not active. <br/><br/>
<strong>Child Nutrition:</strong>  There are 4 to 5 malnourished children in the GP. The village has 3 Anganwadi Centres. Earlier the focus of the AWCs was only meals. With the supportive monitoring of the Sarpanch and Ward members, now the AWC is also providing services such as early childhood education and playing. <br/><br/>
<strong>Education:</strong>  The GP has 1 Primary School with 1-8 Standards, and there are also 2 Ashram Shalas with 9 to 10 Standards. There are a total of 13 teachers and 285 students in all these institutions.The children in 10th and 12th standards used to fail in exams, followed by dropping out, which indicated poor quality of school education. So the Sarpanch introduced tuition classes in Science, Maths and English subjects for the children in the 9th standard. As a result, all the children are now passing in 10th standard, and enrolling in 11th standard. <br/><br/>
Mr. Dineshbhai also set up a library in his village for all the children in 2015. It was supported by AKRSP by providing funds for computers and electricity. <br/><br/>
<strong>Leadership:</strong> Mr. Dineshbhai is enthusiastic and has a vision of improving education in the village. AKRSP has also been supporting the Gram Panchayat in preparation and implementation of GPDP. The GP receives Rs. 30 lakh grant from the Finance Commission per year. MGNREGA grant is Rs. 60 lakh. There are also other state and central government schemes with grants. The GP has an income of Rs. 1.5 lakh from house and sanitation tax. The GP human resources include Talati, Peon and VCE.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf:require("../../res/GP Stories/Khokhra Umar GP.pdf"),
    },
    {
      id: 10,
      name: "Kundiamba",
      desc: "A Tribal Gram Panchayats struggle and success in child nutrition",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/book5.jpg'),
      gpImgMobile: require('../../assets/gp-success/mbook5.jpg'),
      content: <React.Fragment>Kundiamba Gram Panchayat (population as per 2011 Census: total 1,583, females 794, males 789) of Dediapada Block is located in Narmada district in the state of Gujarat, India. The village is located at about 58 KM from Rajpipla (district headquarters), and 13 KM from Dediapada, the nearest town and the block headquarters. Childrens population below 6 years includes a total of 246 (female 110, male 136). The village is populated mainly by tribal people, and others include only 27 persons (16 females, 11 males).<br/><br/>

<strong>Governance:</strong> Mr. Maganbhai has undertaken various work such as roads, drinking water supply, land levelling, wells, soak pits near hand pumps for water recharge, kitchen garden and child nutrition. <br/><br/>
<strong>Child Nutrition:</strong> There are 3 AWCs in the village. There are 3 to 4 children in Severely Acute Malnourished (SAM) and 10 to 12 children in Moderately Acute Malnourished (MAM) categories in the village currently, and it is usual every year. He, along with other Panchayat members, thought that the childrens malnourishment can be addressed by providing eggs as meal in the AWC. (It may be noted that, in this tribal area, the people eat non-vegetarian food and vegetables are scarce due to lack of irrigation facilities). The Sarpanch proposed to the ICDS department at the block level to include eggs in the AWC meal menu. However, the decision could not be reached at the block and the district levels. The Sarpanch then escalated it to the state level. There was a huge reluctance at the state level also for including eggs in the AWC meal menu (as Gujarat is known for vegetarianism). It took a few months before the state government delivered the guidance that the local communities may take up a suitable decision based on the local culture. It was a win for the GP in the beginning of 2020. The AWC started serving eggs in early 2020, but then there was a lockdown due to Covid-19, and the AWCs were closed. Therefore, the implementation was disrupted and results were not available for childrens growth. <br/><br/>
<strong>Education:</strong> Mr. Maganbhai also supported the village primary school by developing a sports ground for children. Library has also been set up with AKRSPs support. (AKRSP - Aga Khan Rural Support Program - India). <br/><br/>
<strong>Women empowerment:</strong> There are 10 Sakhi Mandals in the village under Mission Mangalam and 3 of them are active. <br/><br/>
<strong>Public Health:</strong> In Covid-19 situation, the GP took actions such as fumigation and restricted entry into the village for the outsiders. There is no positive Covid case in the GP so far.  <br/><br/>
<strong>Other:</strong> Under MGNREGA, various works have been taken up such as metal road, ponds deepening or renovation, compost pit, cattle shed etc. Rs. 60 to 70 lakh were spent in such works. <br/><br/>

<strong>Leadership:</strong> Mr. Maganbhai is an active leader. Usually 3 Gram Sabhas take place in a year, and 50 to 100 people participate in each. <br/><br/>

<strong>Future plans:</strong>

The challenges include collection of peoples contribution in various schemes, as the economic condition of most of the households is poor.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf: require("../../res/GP Stories/Kundiamba.pdf"),
    },
    {
      id: 11,
      name: "Dumalpur school",
      desc: "Offers a practical example of Bal Sansand",
      avatar: require('../../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../../assets/gp-success/gp11.png'),
      gpImgMobile: require('../../assets/gp-success/mgp11.png'),
      content: <React.Fragment>Dumalpur, a village under Kapalsadi Gram Panchayat (population as per 2011 Census: total 2,024, females 986, males 1,038) of Jhagadia Block is located in Bharuch district in the state of Gujarat, India. The village is located at about 18 KM from Bharuch (district headquarters), and 15 KM from Jhagadia, the nearest town and the block headquarters. Childrens population below 6 years includes a total of 208 (female 108, male 100). The village is populated mainly by tribal people.<br/><br/>

<strong>Bal Sansad:</strong> Bal Sansad (childrens parliament) is the term used for formation of school childrens parliament through democratic election process annually. The process is to be conducted by the Head Teacher along with other school teachers. Purpose of the Bal Sansad process is to introduce the democratic process to the children, promote leadership skills among them, and also promote discipline of carrying out the responsibilities assigned to them. <br/><br/>
Similar to the national parliament, the student getting the most number of votes is declared as Prime Minister (or President), followed by announcements of other Ministers depending upon their vote counts. The ministers, according to their interests, may be allocated various departments such as health, education, sanitation, water etc. Sample guidelines for organising Bal Sansad have been attached in Hindi and Gujarati for ready reference and their web links, respectively, are as follows: <br/><br/>
<a href="http://sujal-swachhsangraha.gov.in/sites/default/files/Unicef_Bal_sansad_book_0.pdf" target="_blank" rel="noreferrer">Unicef Bal Sansad Book </a>&nbsp;and&nbsp;
<a href="http://sujal-swachhsangraha.gov.in/sites/default/files/2015_WiS_Bal%20Sansad%20Guideline_Guidelines_Gujarati_Gujarat.pdf" target="_blank" rel="noreferrer">2015 WiS Bal Sansad Guideline Guidelines Gujarati Gujarat</a>. <br/><br/>

Dumalpur is a tribal village in Jhagadiya block of Bharuch district in Gujarat. It has a primary school with Standards 1 to 8 in the village. Since 2016, when Mukeshbhai Kalidasbhai Vankar became the Head Teacher, he initiated the Bal Sansad concept. There is also a Government of Gujarat guideline (but not mandatory). In Dumalpur primary school, Bal Sansad election is held every year during the month of June or July (as the new academic year starts from June). The students are encouraged to participate in the election process by registering their names for candidacy. The students in Standard 8 are usually more interested in the election and positions. <br/><br/>
Elections are organised in the same manner as for national/state elections. The candidates are allowed to choose their symbols and conduct campaigns for votes. On the day of voting, election booth is created with children playing the role of polling officers and guards. The students cast their votes through stamping the sign of their chosen candidate. Votes counting is done in transparent manner. <br/><br/>
The candidate with the highest votes becomes the President, followed by Vice-President and Ministers for departments such as Sanitation, Health, Midday Meal and others. The President plays the leadership role. For example, Rakesh Mangabhai Vasava, Standard 8 student, had become President and he played excellent role of motivating other ministers to do their work effectively. <br/><br/>
Thus the children are exposed to the democratic process of election. They get the opportunities to develop their democratic leadership and follower-ship skills right from the school age. The Dumalpur primary school links this Bal Sasad with the School Management Committee but not the GPDP yet, which is an opportunity in coming years. Other schools may also follow the suit, and Sarpanch can encourage the schools. <br/><br/>
It is important to note that the Bal Sansads role, as given in the above guidelines, has been envisaged as limited to the school management, and has no role in Gram Panchayat or local governance. Furthermore, Bal Sansad does not represent all the children of the village, especially those who are school drop out (especially adolescent girls), child labour, and children studying outside the GP if the GP has schools only up to Standard 8. Therefore, there is a need for Bal Panchayat to play the role in GP and GPDP, and if Bal Sansad exists in the GP, the Bal Sansad can be merged with Bal Panchayat.
</React.Fragment>,
      link: "/GP-Success-Stories/GP-Details",
      pdf: require("../../res/GP Stories/Dumalpur school.pdf"),
    },
  ];

  return gpArray;
}
